import React from 'react';
import { Routes, Route, BrowserRouter, useLocation } from "react-router-dom";
import { AnimatePresence, motion } from 'framer-motion';

import 'App.css';
import Login from 'components/LoginPage/Login';
import CreateUser from 'components/CreateUserPage/CreateUser';
import CreateUserAccountLink from 'components/CreateUserPage/CreateUserAccountLink';
import {JWTAuthRoute} from 'utils/JWTAuthRoute'; // utile.js  
import Main from 'components/MainPage/Main';

const nextTransition = {
  initial: { opacity: 0, x: 100 },   //  
  animate: { opacity: 1, x: 0 },     //   
  exit: { opacity: 0, x: -100 },     //  
};

const backTransition = {
  initial: { opacity: 0, x: -100 },  //  
  animate: { opacity: 1, x: 0 },     //   
  exit: { opacity: 0, x: 100 },      //  
};

function AnimatedRoutes() {
  const location = useLocation();
  const isBack = location.state?.isBack || false; //    

  const transition = isBack ? backTransition : nextTransition;

  return (
    <AnimatePresence mode="wait">
      <Routes location={location} key={location.key}>
        {/*   */}
        <Route
          path="/"
          element={
            <motion.div
              initial="initial"
              animate="animate"
              exit="exit"
              variants={transition}
            >
              <Login />
            </motion.div>
          }
        />
        {/*    */}
        <Route
          path="/components/CreateUserPage/CreateUser"
          element={
            <motion.div
              initial="initial"
              animate="animate"
              exit="exit"
              variants={transition}
            >
              <CreateUser />
            </motion.div>
          }
        />
        {/*     */}
        <Route
          path="/components/CreateUserPage/CreateUserAccountLink"
          element={
            <motion.div
              initial="initial"
              animate="animate"
              exit="exit"
              variants={transition}
            >
              <CreateUserAccountLink />
            </motion.div>
          }
        />
        {/*      */}
        <Route
          path="/components/FindByIDPage/FindByID"
          element={
            <motion.div
              initial="initial"
              animate="animate"
              exit="exit"
              variants={transition}
            >
              <CreateUser />
            </motion.div>
          }
        />
        {/*     */}
        <Route
          path="/components/MainPage/Main"
          element={
            <motion.div
              initial="initial"
              animate="animate"
              exit="exit"
              variants={transition}
            >
              <JWTAuthRoute>
                <Main />
              </JWTAuthRoute>
            </motion.div>
          }
        />
      </Routes>
    </AnimatePresence>
  );
}

function App() {
  return (
    <BrowserRouter>
      <AnimatedRoutes />
    </BrowserRouter>
  );
}

export default App;
