import React, { Component, useState, useEffect } from 'react';
import { useNavigate } from "react-router-dom";
import axios from "axios";
import { Cryption } from 'utils/Cryption'; // utile.js  

import {GoogleLogin} from "@react-oauth/google";
import {GoogleOAuthProvider} from "@react-oauth/google";
import { jwtDecode } from "jwt-decode"; //    decode

const googleConfig = {
    clientID: process.env.REACT_APP_GOOGLE_OAUTH_CLIENTID,
    clientPWD: process.env.REACT_APP_GOOGLE_OAUTH_CLIENTPWD,
};

let requestData = {};

function GoogleCreateUser(data) {
    /* 
      - aud : ID      
      - azp : ID         
      - email :  
      - email-verified :    ?
      - exp : ID  
      - family-name :  last-name
      - given-name :  first-name
      - iat : ID   
      - iss : ID    URL   accounts.google.com
      - jti : ID   ,    
      - name :   
      - nbf : ID  ,  
      - picture :   url
      - sub :   ,     
    */
    //     
    const EncryptUserID = Cryption('encrypt', data.email);
    requestData = {
        "userID": EncryptUserID,
        "userPWD": data.email,
        "userPWDConfirm": data.email,
        "userName": data.name,
        "userPhone": "",
        "userEmail": data.email,
        "userAlias": "",
        "userAccountLink": "google",
    };

    const verifyID = async () => {
        try {
            const res = await axios({
                method: "POST",
                url: 'http://localhost:8080/' + process.env.REACT_APP_USER_URL + '/findbyid',
                data: requestData,
                headers: { 'Content-type': 'application/json' }
            });

            const result = res.data;
            const resultMessage = result.result;

            if (resultMessage !== 'Success') {
                if (resultMessage === 'Not Exsits') {
                    alert("  .\n    .");
                    return "Create";
                } else {
                    alert(resultMessage);
                    return "error";
                }
            } else {
                return "Login";
            }
        } catch (error) {
            alert(' : ' + error.message);
            return false;
        }
    };

    return verifyID(); // async    
}

function doLogin(data) {
    const requestData = {
        userID : Cryption('encrypt', data.userID),
        userPWD : Cryption('encrypt', data.userPWD),
    };

    let url = 'http://localhost:8080/'+process.env.REACT_APP_USER_URL+'/dologin';

    const login = async () => {
        try {
                const res = await axios({
                method: "POST",
                url: url,
                data: requestData,
                // header JSON    
                headers: {'Content-type': 'application/json'},
                withCredentials: true // CROS true
            });

        let result = res.data;
        let resultMessage = result.result;
        let resultData = result.data;
        if (resultMessage != 'Success') {
            alert(resultMessage);
        } else {
            const accessToken = res.headers.get('Access');
            if (accessToken) {
            localStorage.setItem('accessToken', accessToken);
            }
            return "Success";
            //setMemberID(resultData.memberID);
            //setMemberName(resultData.memberName);
            //setMemberEmail(resultData.memberEmail);
            //setStatus('complelte');
        }
        } catch(error){
            alert(error);
        }
    };

    return login();
}

function GoogleLoginComponent(props) {
    const navigate = useNavigate();

    let googleContents = [];

    googleContents.push(
        <GoogleOAuthProvider clientId={googleConfig.clientID}>
            <GoogleLogin
                onSuccess={async (res) => {
                    let googleResult = jwtDecode(res.credential);
                    const createGoogleUserResult = await GoogleCreateUser(googleResult); // GoogleCreateUser   
                    if (createGoogleUserResult == "Create") {
                        // navigate  state   
                        requestData = {
                            ...requestData,
                            "userID": googleResult.email,
                        };
                        navigate("/components/CreateUserPage/CreateUserAccountLink", { state: { userAccountData: requestData } });
                    } else if (createGoogleUserResult == "Login") {
                        requestData = {
                            ...requestData,
                            "userID": googleResult.email,
                        };
                        
                        const result = await doLogin(requestData);
                        if ("Success" == result)
                          navigate("/components/MainPage/Main");
                    }
                }}
                onFailure={(err) => {
                    alert(err);
                }}
            />
        </GoogleOAuthProvider>
    );

    return (<div>{googleContents}</div>);
}

export default GoogleLoginComponent;